#intro{
    height: calc(100vh -4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
}
.bg{
    position:absolute;
    right:0px;
    top: 4rem;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}
.introContent
{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font: 1.75rem;
    font-weight: 100;
}
.introName{
    color:yellow ;
}
.intropara{
    font-size: medium;
    font-weight: 100;
    opacity: 0.8;

}
.btn{
    background-color:white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}
@media screen and (max-width:840px) {
    .bg{
            display: none;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
    
}

@media screen and (max-width:540px) {
    #intro{
        height:fit-content;
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .introContent{
        height: fit-content;
    }
}